	            text: ["Pillow Talk aims to empathy visitors’ childhood memories that lay on bed with careful and listen to late-night broadcast in secret. A vertical bed is set up as the installation. Visitors can lean on it as lay on a bed. Through a pillow, they can hear something and response the questions.", "However, restricted by cost and time, we can’t build an installation above two. We followed the folktale’s framework, make a cardboard tree."]
