	        content: ["In the Exhibition, people like the tree because the light makes the tree look shining. The color-change tree holes catch many people's eyes, especially kids'. They are curious about what is in the tree hole. But, since we aim to deliver a novel and private communication experience, the interaction seems hidden, if they didn't know the tree-hole folktale before. ", "To introduce our strange concept and technology to people, we design and set up a tutorial-like experience place for them - watching the video with covering ear by hands to listening to the narrative. However, in the peak time, there are so many people coming that most of them can't follow our default process to experience our project, which make some of them feel confused that how the tree works."]
